import React from 'react'

import styles from './MyTable.module.scss'

const MyTable = ({className, children, ...props}) => {

    const cls = [
        styles.MyTable,
        className
    ]

    return (
        <div className={cls.join(' ')}>
            <table>
                <tbody>
                    {children}
                </tbody>
            </table>
        </div>
    )
}

export default MyTable