import React from 'react'

import styles from './DetailsTableRow.module.scss'

const DetailsTableRow = ({
  className, data, label, ...props
}) => {

    const cls = [
        styles.DetailsTableRow,
        className
    ]

    return (
        <tr className={cls.join(' ')}>
                    <th>

                        {label}
                    </th>
                    <td>
                      <span>
                        {data}
                      </span>
                    </td>
        </tr>
    )
}

export default DetailsTableRow