import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { Breadcrumb, BreadcrumbItem, Button, Form, FormGroup, Input, Label } from 'reactstrap'
import { getAccountDetailsThunk } from '../../../redux/actions/accountActions'

import styles from './AccountDetails.module.scss'



const AccountDetails = ({
  details,
  getAccountDetailsThunk,
  match,
  history

}) => {

  const [controls, setControls] = useState({
    email: '',
    login: '',
    phone_number: '',
    deleted: '',
    blocked: '',
    suspended_until: '',
    verification_required: '',
    role_names: '',
  })
  const [touched, setTouched] = useState(false)

  const onSubmit = (e) => {
    e.preventDefault()
    console.log(controls);
  }

  const onControlChange = (e) => {
    const control = e.target
    setControls((controls) => {
      return {
        ...controls,
        [control.name]: control.value
      }
    })
    setTouched(true)
  }

  const onCheckboxChange = (e) => {
    const checkbox = e.target
    setControls((controls) => {
      return {
        ...controls,
        [checkbox.name]: checkbox.checked
      }
    })
  }

  useEffect(() => {
    getAccountDetailsThunk(match.params.id)
    // eslint-disable-next-line
  }, [])

  useEffect(() => {
    setControls((controls) => {
      // const detailsInfo = {}
      // Object.keys(controls).forEach(key => {
      //   detailsInfo[key] = details[key] || ''
      // })
      return {
        ...controls,
        ...details
      }
    })
  }, [details])

  const {email, login, phone_number, deleted, 
    blocked, suspended_until, verification_required, role_names
  } = controls

  if (!details) {
    return (
      <h1>  </h1>
    )
  }

    return (
        <div className={styles.AccountDetails}>
        <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/accounts' exact></BreadcrumbItem>
          <BreadcrumbItem active tag="span">  {details && details.login}</BreadcrumbItem>
        </Breadcrumb>
            <h1>  {details && details.login}</h1>
            <Form onSubmit={onSubmit} >
              <FormGroup>
                <Label for="login"></Label>
                  <Input
                    type="text"
                    name="login"
                    id="login"
                    placeholder=""
                    value={login}
                    onChange={onControlChange}
                    required
                  />
              </FormGroup>
                  
              <FormGroup>
                <Label for="email">Email</Label>
                <Input
                  //  invalid={touched.email && !email}
                  type="email"
                  name="email"
                  id="email"
                  placeholder="email"
                  value={email}
                  onChange={onControlChange}
                />
              </FormGroup>
              <FormGroup>
                <Label for="phone_number"></Label>
                <Input
                  type="text"
                  name="phone_number"
                  id="phone_number"
                  placeholder="phone_number"
                  value={phone_number}
                  onChange={onControlChange}
                />
              </FormGroup>
              <FormGroup check>
                <Label check for="deleted">
                 
                  <Input
                  type="checkbox"
                  name="deleted"
                  id="deleted"
                  checked={deleted}
                  onChange={onCheckboxChange}
                />
                 {' '}   
                  </Label> 
                
              </FormGroup>
             
              <Button 
              disabled={!(email && login && touched)}
              color='success'
              ></Button>
            </Form>
            
        </div>
    )
}

const mapStateToProps = (state) => {
  return {
      details: state.accounts.details,
  }
}

export default connect(mapStateToProps, {getAccountDetailsThunk})(AccountDetails)