import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { useHistory, useParams } from 'react-router'
import { NavLink } from 'react-router-dom'
import { Breadcrumb, BreadcrumbItem, Button } from 'reactstrap'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import Spinner from '../../../components/Spinner/Spinner'
import MyTable from '../../../components/UI/MyTable/MyTable'
import TableInput from '../../../components/UI/MyTable/TableInput/TableInput'
import { useAlert } from '../../../hooks/useAlert'
import { useFormAndValidation } from '../../../hooks/useFormValiation'
import { getPartnerDetailsThunk, setPartnerDetails, updatePartnerThunk } from '../../../redux/actions/partnerActions'

import styles from './PartnerUpdate.module.scss'

const PartnerUpdate = ({
  className, getPartnerDetailsThunk, setPartnerDetails, updatePartnerThunk,
  details, ...props
}) => {

  const { successAlertVisible, setSuccessAlertVisible, successMessage,
    failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage, checkRes} = useAlert()



  const {values, handleChange, errors, isValid, resetForm, setValues, setIsValid } = useFormAndValidation()
  const {id} = useParams()
  const history = useHistory()

  useEffect(() => {
    getPartnerDetailsThunk([id])
    .then(checkRes(false))
    .catch((err) => console.log(err))
    return () => {
        setPartnerDetails({
            uuid: '',
            inn: '',
            name: ''
        })
    }
    // eslint-disable-next-line 
  }, [])

  useEffect(() => {
    setIsValid(false)
    // eslint-disable-next-line
}, [])
  useEffect(() => {
    setValues(details)
    // eslint-disable-next-line
}, [details])


  const onSubmit = (evt) => {
    evt.preventDefault()
    updatePartnerThunk({uuid: id, ...values})
    .then((res) => {
      checkRes()(res)
      return res
    })
    .then(res => {
        if (res.ok) {
            setTimeout(() => {
                history.goBack()
            }, 3000)
        }
    })
    .catch((err) => console.log(err))
    console.log(values);
  }

    const cls = [
        styles.PartnerUpdate,
        className
    ]

    if (!details) {
      return <h1>  </h1>
  }

  if (!details.uuid) {
      return <Spinner/>
  }

    return (
        <div className={cls.join(' ')}>
         <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/partners' exact> </BreadcrumbItem>
          <BreadcrumbItem tag={NavLink} to={`/partners/${id}`} exact> </BreadcrumbItem>
          <BreadcrumbItem active tag="span"> </BreadcrumbItem>
        </Breadcrumb>
            <h1>   </h1>
            <form onSubmit={onSubmit}>
              <MyTable>
                <TableInput
                    name='inn'
                    label=''
                    required={true}
                    onChange={handleChange}
                    value={values.inn}
                    errorText={errors.inn}
                    minLength={10}
                    maxLength={12}
                    
                />
                <TableInput
                    name='name'
                    label=''
                    required={true}
                    onChange={handleChange}
                    value={values.name}
                    errorText={errors.name}
                    minLength={1}
                    maxLength={199}
                />
              </MyTable>
              <Button
                 color={isValid? "success": 'secondary'}
                  disabled={!isValid}
              >
                
              </Button>
            </form>

            <SuccessAlert
                visible={successAlertVisible}
                setVisible={setSuccessAlertVisible}
                message={successMessage}
            />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />

        </div>
    )
}

const mapStateToProps = (state) => {
  return {
      details: state.partners.details,
  }
}


export default connect(
  mapStateToProps, {getPartnerDetailsThunk, setPartnerDetails, updatePartnerThunk})(PartnerUpdate)