import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { useHistory, useParams } from 'react-router'
import { NavLink } from 'react-router-dom'
import { Breadcrumb, BreadcrumbItem } from 'reactstrap'
import FlexBlock from '../../../components/Blocks/FlexBlock/FlexBlock'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import Spinner from '../../../components/Spinner/Spinner'
import MyButton from '../../../components/UI/MyButton/MyButton'
import DetailsTableRow from '../../../components/UI/MyTable/DetailsTableRow/DetailsTableRow'
import MyTable from '../../../components/UI/MyTable/MyTable'
import { useAlert } from '../../../hooks/useAlert'
import { deleteTerminalsThunk, getTerminalDetailsThunk } from '../../../redux/actions/terminalActions'

import styles from './TerminalDetails.module.scss'

const TerminalDetails = ({
  className, details, getTerminalDetailsThunk, deleteTerminalsThunk,
  ...props}) => {

  const { id } = useParams()
  const history = useHistory()

  const { successAlertVisible, setSuccessAlertVisible, successMessage,
    failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage, checkRes} = useAlert()


  useEffect(() => {

    getTerminalDetailsThunk([id])
    .then(checkRes(false))

    return () => {
      // setShopDetails({
      //     uuid: '',
      //     partner_uuid: '',
      //     name: '',
      //   })
    }
    // eslint-disable-next-line 
  }, [])

  const onDelete = () => {
    const confirmed = window.confirm("   ?");
    if (confirmed) {
      deleteTerminalsThunk([id])
      .then((res) => {
        checkRes()(res)
        return res
      })
      .then(res => {
          if (res.ok) {
              setTimeout(() => {
                history.goBack()
              }, 1000)
          }
      })
      .then(() => console.log('deleteTerminalsThunk'))
      .catch((err) => console.log(err))
    }
  }

    const cls = [
        styles.TerminalDetails,
        className
    ]

    if (!details) {
      return <h1>  </h1>
    }

    if (!details.uuid) {
        return <Spinner/>
    }

    return (
        <div className={cls.join(' ')}>
         <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/partners' exact> </BreadcrumbItem>
          <BreadcrumbItem active tag="span">  &nbsp; <strong>{details.name}</strong></BreadcrumbItem>
        </Breadcrumb>
             <center><h1>   </h1></center>
            <MyTable>
                <DetailsTableRow
                    label='uuid'
                    data={details.uuid}
                />
                <DetailsTableRow
                    label=''
                    data={details.acquirer_uuid}
                />
                <DetailsTableRow
                    label=''
                    data={(details.active && '') || ''}
                />
                <DetailsTableRow
                    label=' '
                    data={details.currency_num}
                />
                <DetailsTableRow
                    label=''
                    data={details.name}
                />
                <DetailsTableRow
                    label='id '
                    data={details.config_id}
                />
                 <DetailsTableRow
                    label=''
                    data={details.operations.join(' ').toUpperCase()}
                />
        </MyTable>


       <FlexBlock>
          <MyButton
                color={'secondary'}
                onClick={() => history.push(`/terminals/update/${id}`)}
                buttonText=''
            />
            <MyButton
                color={'danger'}
                onClick={onDelete}
                buttonText=''
            />
       </FlexBlock>
       
            <SuccessAlert
                    visible={successAlertVisible}
                    setVisible={setSuccessAlertVisible}
                    message={successMessage}
                />

            <FailureAlert
                visible={failureAlertVisible}
                setVisible={setFailureAlertVisible}
                message={failureMessage}
            />

        </div>
    )
}

const mapStateToProps = (state) => {
  return {
    details: state.terminals.details,
  }
  }

  

export default connect(mapStateToProps, {
  getTerminalDetailsThunk, deleteTerminalsThunk, 
 })(TerminalDetails)