import React from 'react'

import styles from './ClickableTH.module.scss'

const ClickableTH = ({className, label, onClick, ...props}) => {

    const cls = [
        styles.ClickableTH,
        className
    ]

    return (
        <th onClick={onClick} className={cls.join(' ')}>
           {label}
        </th>
    )
}

export default ClickableTH