import { ACCOUNTS_LOADING, GET_ACCOUNTS_DETAILS, GET_ACCOUNTS_FAILURE, GET_ACCOUNTS_SUCCESS } from "../actions/actionTypes";

const initialState = {
  accounts: [],
  loading: false,
  errors: {
    accounts: null,
    details: null,
    update: null,
    delete: null,
    create: null,
  },
  details: {},
}

export default function accountReducer(state = initialState, action) {
  switch (action.type) {
    case GET_ACCOUNTS_SUCCESS:
      return {
        ...state,
        accounts: action.accounts
      }
    case ACCOUNTS_LOADING:
      return {
        ...state,
        loading: action.status
      }
    case GET_ACCOUNTS_FAILURE:
      return {
        ...state,
        errors: {
          ...state.errors,
          ...action.errors
        }
      }
      case GET_ACCOUNTS_DETAILS:
        return {
          ...state,
          details: action.details
        }

    default:
      return state;
  }
}