import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { Table, Pagination, PaginationItem, PaginationLink, Container  } from 'reactstrap'
import { getPartnersThunk } from '../../redux/actions/partnerActions'
import MyTableForm from '../../components/UI/MyTable/MyTableForm/MyTableForm'

import styles from './Partners.module.scss'
import TableInput from '../../components/UI/MyTable/TableInput/TableInput'
import { useFormAndValidation } from '../../hooks/useFormValiation'
import { getTrimmedValues } from '../../utils/utils'
import MyButton from '../../components/UI/MyButton/MyButton'
import ClickableTH from '../../components/UI/MyTable/ClickableTH/ClickableTH'
import ReactPaginate from 'react-paginate'
import SuccessAlert from '../../components/MyAlert/SuccessAlert/SuccessAlert'
import FailureAlert from '../../components/MyAlert/FailureAlert/FailureAlert'
import { useAlert } from '../../hooks/useAlert'


const Partners = ({className, getPartnersThunk, partners=[], ...props}) => {

  const {values, handleChange, errors, isValid, } = useFormAndValidation()
  const { successAlertVisible, setSuccessAlertVisible, successMessage,
    failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage, checkRes} = useAlert()

  const [order, setOrder] = useState(true)
  const [limit, setLimit] = useState(3)
  const [offset, setOffset] = useState(0)

  const changeOrder = (field) => {
    setOrder(!order)

    const filterData = getTrimmedValues(values)

    const sortedData = {
        ...filterData,
        "sort_by": [
            {
              field,
              order: order? 'asc' : 'desc'
            }
        ]
    }
    
    getPartnersThunk(sortedData)

}

    const handlePageClick = (data) => {

        console.log(data);

        const filterData = getTrimmedValues(values)

        setOffset(data.selected * (filterData.limit || 3))

        getPartnersThunk({
            limit: filterData.limit || 3,
            ...filterData,
            offset
        })
    }


  const onSubmit = (evt) => {
    evt.preventDefault()

    const filterData = getTrimmedValues(values)

    if (filterData.limit) {
        filterData.limit = Number(filterData.limit)
    }

    getPartnersThunk(filterData)
    .then(checkRes(false))
    .then(() => console.log('filter getPartnersThunk'))
    console.log(filterData);
  }


    const history = useHistory()

    const cls = [
        styles.Partners,
        className
    ]

    useEffect(() => {
        getPartnersThunk()
        .then(checkRes(false))
        // eslint-disable-next-line
    }, [])

    return (
        <div className={cls.join(' ')}>
            <center><h1> </h1></center>
            <MyTableForm
                onSubmit={onSubmit}
                isValid={isValid}
                buttonText=''
            >
                <TableInput
                    name='inn'
                    label=''
                    onChange={handleChange}
                    value={values.inn}
                    errorText={errors.inn}
                    minLength={1}
                    maxLength={12}
                    
                />
                <TableInput
                    name='name'
                    label=''
                    onChange={handleChange}
                    value={values.name}
                    errorText={errors.name}
                    minLength={1}
                    maxLength={199}
                />
                <TableInput
                        type='number'
                        name='limit'
                        label='- '
                        onChange={handleChange}
                        value={values.limit}
                        errorText={errors.limit}
                        style={{
                            width: `140px`
                        }}
                        min={1}
                        max={100}
                    />
            </MyTableForm>

            <Table hover>
                <thead>
                    <tr>
                        <th></th>
                        <th>uuid</th>
                        <ClickableTH
                            onClick={() => changeOrder('inn')}
                            label=''
                        />
                        <ClickableTH
                            onClick={() => changeOrder('name')}
                            label=''
                        />
                    </tr>
                </thead>
                <tbody>
                    {
                        partners?.length?
                         partners.map((item, i) => {
                            return (
                                <tr 
                                    key={item.uuid}
                                    onClick={() => history.push(`/partners/${item.uuid}`)}
                                >
                                    <td>{i+1}</td>
                                    <td>{item.uuid}</td>
                                    <td>{item.inn}</td>
                                    <td>{item.name}</td>
                                </tr>
                            )
                        }) : (
                            <tr>
                                <th>
                                    <h1>  </h1>
                                </th>
                            </tr>
                        )
                    }
                    
                    
                </tbody>
            </Table>

            <div className={styles.tableBotton} >

            <MyButton
                color={'primary'}
                onClick={() => history.push(`/add-partner`)}
                buttonText=' '
            />

            <Pagination className={styles.Pagination} aria-label="Page navigation">
                <PaginationItem>
                    <PaginationLink first href="#" />
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink previous href="#" />
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink href="#">
                    1
                    </PaginationLink>
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink href="#">
                    2
                    </PaginationLink>
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink href="#">
                    3
                    </PaginationLink>
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink href="#">
                    4
                    </PaginationLink>
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink href="#">
                    5
                    </PaginationLink>
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink next href="#" />
                </PaginationItem>
                <PaginationItem>
                    <PaginationLink last href="#" />
                </PaginationItem>
            </Pagination>

            {/* <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                breakLabel={'...'}
                breakClassName={'break-me'}
                pageCount={15/limit}
                marginPagesDisplayed={2}
                pageRangeDisplayed={5}
                onPageChange={handlePageClick}
                containerClassName={'pagination'}
                activeClassName={styles.active}
                pageClassName={styles.pageClass}
                /> */}

            </div>

           
            <SuccessAlert
                visible={successAlertVisible}
                setVisible={setSuccessAlertVisible}
                message={successMessage}
            />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />
           

        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        partners: state.partners.partners
    }
}



export default connect(mapStateToProps, {
     getPartnersThunk 
})(Partners)