import React from "react";
import { Nav, NavItem } from 'reactstrap';
import MyDropDown from "../UI/MyDropDown/MyDropDown";
import MyLink from "../UI/MyLink/MyLink";

import styles from "./SideMenu.module.scss";

const SideMenu = (props) => {
  return (
    <div className={styles.SideMenu}>
      <MyDropDown title={` `}>
        <Nav vertical>
            <NavItem>
              <MyLink to="/accounts"></MyLink>
            </NavItem>
            <NavItem>
              <MyLink to="/create-account"> </MyLink>
            </NavItem>
          </Nav>
      </MyDropDown>
      <MyDropDown title={''}>
        <Nav vertical>
            <NavItem>
              <MyLink to="/partners"></MyLink>
            </NavItem>
            <NavItem>
              <MyLink to="/acquirers"></MyLink>
            </NavItem>
        </Nav>
      </MyDropDown>
      <MyDropDown title={''}>
        <Nav vertical>
            <NavItem>
              <MyLink to="/transactions"></MyLink>
            </NavItem>
        </Nav>
      </MyDropDown>

   
 
    </div>
  );
};

export default SideMenu;
