import React, { useState, useEffect } from 'react'
import MyTableForm from '../../../components/UI/MyTable/MyTableForm/MyTableForm'
import TableInput from '../../../components/UI/MyTable/TableInput/TableInput'
import { useFormAndValidation } from '../../../hooks/useFormValiation'
import TableMultiselect from '../../../components/UI/MyTable/TableMultiselect/TableMultiselect'

import styles from './TerminalsAddition.module.scss'
import { useParams } from 'react-router'
import { connect } from 'react-redux'
import { getAcquirersThunk } from '../../../redux/actions/acquirerActions'
import { createTerminalThunk } from '../../../redux/actions/terminalActions'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'
import { useAlert } from '../../../hooks/useAlert'
import { Breadcrumb, BreadcrumbItem } from 'reactstrap'
import { NavLink } from 'react-router-dom'



const TerminalsAddition = ({
    className, getAcquirersThunk, acquirers, createTerminalThunk,
    ...props}) => {

    const {shopId} = useParams()

    const { successAlertVisible, setSuccessAlertVisible, successMessage,
        failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage, checkRes} = useAlert()
    

    const {values, handleChange, errors, isValid, resetForm, setValues, setIsValid } = useFormAndValidation()
    
    const [bankOptions, setBankOptions] = useState([{name: ' ...', avtive: true}])
    const activeOptions = [
        {name: '', value: true, jsonKey: 'active'},
        {name: '', value: false, jsonKey: 'active'},
    ]
    
    const operationsOptions = [
        {name: 'AFT', value: 'aft', jsonKey: 'operations'},
        {name: 'AFT ', value: 'aft_recurring', jsonKey: 'operations'},
        {name: 'ECOM', value: 'ecom', jsonKey: 'operations'},
        {name: 'ECOM ', value: 'ecom_recurring', jsonKey: 'operations'},
        {name: 'ECOM  ', value: 'ecom_recurring_reg', jsonKey: 'operations'},
        {name: 'OCT (B2C) -   ', value: 'oct_card', jsonKey: 'operations'},
        {name: 'OCT -   ', value: 'oct_account', jsonKey: 'operations'},
    ]

    const currenceOptions = [
        {"value": "643", "code": "RUB", "name": " ", jsonKey: 'currency_num'},
            {"value": "840", "code": "USD", "name": " ", jsonKey: 'currency_num'},
            {"value": "978", "code": "EUR", "name": "", jsonKey: 'currency_num'},
            {"value": "392", "code": "JPY", "name": " ", jsonKey: 'currency_num'},
            {"value": "156", "code": "CNY", "name": "  ", jsonKey: 'currency_num'},
    ]

    const onSingleSelect = (selectedList, {jsonKey, value}) => {
        console.log(selectedList);
        setValues({
            ...values,
            [jsonKey]: value
        })
    }
    const onBankSelect = (selectedList, selectedItem) => {
        console.log(selectedList);
        setValues({
            ...values,
            acquirer_uuid: selectedItem.uuid
        })
    }
    const onMultiSelect = (selectedList, selectedItem) => {
        console.log(selectedList);
        setValues({
            ...values,
            [selectedItem.jsonKey]: selectedList.map(i => i.value)
        })
    }

    const onRemove = (selectedList, removedItem) => {
        console.log(selectedList);
        setValues({
            ...values,
            [removedItem.jsonKey]: selectedList.map(i => i.value)
        })
    }

    useEffect(() => {
        getAcquirersThunk()
        // eslint-disable-next-line
    }, [])
    useEffect(() => {
        if (acquirers) {
            setBankOptions(acquirers)
        }
    }, [acquirers])

    useEffect(() => {
        setIsValid(false)
        // eslint-disable-next-line
    }, [])

    const cls = [
        styles.TerminalsAddition,
        className
    ]

    const onSubmit = (evt) => {
        evt.preventDefault()
        const terminalData = {
            ...values,
            store_uuid: shopId
        }
        console.log(terminalData);

        createTerminalThunk(terminalData)
        .then((res) => {
            checkRes()(res)
            return res
          })
          .then(res => {
              if (res.ok) {
                  setTimeout(() => {
                      resetForm()
                  }, 3000)
              }
          })
          .catch((err) => console.log(err))
        .then(() => console.log('TerminalsAddition'))

    }

    return (
        <div className={cls.join(' ')}>
         <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/partners' exact> </BreadcrumbItem>
          <BreadcrumbItem tag={NavLink} to={`/shops/${shopId}`} exact> </BreadcrumbItem>
          <BreadcrumbItem active tag="span"> </BreadcrumbItem>
        </Breadcrumb>
            <center><h1> </h1></center>
            <MyTableForm
                onSubmit={onSubmit}
                isValid={isValid}
            >
                    <TableInput
                         name='name'
                        label=''
                        required={true}
                        onChange={handleChange}
                        value={values.name}
                        errorText={errors.name}
                        minLength={1}
                        maxLength={199}
                    />
                    <TableMultiselect
                        label=''
                        options={bankOptions}
                        displayValue="name"
                        onSelect={onBankSelect}
                        onRemove={onRemove}
                        placeholder=''
                        singleSelect
                    />
                     <TableMultiselect
                        label='A'
                        options={activeOptions}
                        displayValue="name"
                        onSelect={onSingleSelect}
                        onRemove={onRemove}
                        placeholder=''
                        singleSelect
                    />
                    <TableMultiselect
                        label=''
                        options={operationsOptions}
                        displayValue="name"
                        onSelect={onMultiSelect}
                        onRemove={onRemove}
                        placeholder=''
                    />
                    <TableMultiselect
                        label=' '
                        options={currenceOptions}
                        displayValue="name"
                        onSelect={onSingleSelect}
                        onRemove={onRemove}
                        placeholder=''
                        singleSelect
                    />
                    <TableInput
                         name='config_id'
                        label='id '
                        required={true}
                        onChange={handleChange}
                        value={values.config_id}
                        errorText={errors.config_id}
                        minLength={1}
                        maxLength={199}
                    />
            </MyTableForm>

            <SuccessAlert
                visible={successAlertVisible}
                setVisible={setSuccessAlertVisible}
                message={successMessage}
            />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        acquirers: state.acquirers.acquirers,

    }
}



export default connect(mapStateToProps, {
     getAcquirersThunk, createTerminalThunk,
    })(TerminalsAddition)