import React, { useEffect, useState } from 'react'
import MyInput from '../../MyInput/MyInput'

import styles from './TableInput.module.scss'

const TableInput = ({className, label, ...props}) => {
    const [inputId, setInputId] = useState('')

    useEffect(() => {
      setInputId(`${label}${String(Math.random()).slice(2)}`)
    }, [label])

    const cls = [
        styles.TableInput,
        className
    ]

    return (
        <tr className={cls.join(' ')}>
              <th>
                <label htmlFor={inputId}>{label}</label>
              </th>
              <td>
                  <MyInput
                      {...props}
                      id={inputId}
                      // name={name}
                      placeholder={label}
                      // required={required}
                      // onChange={onChange}
                      // value={value}
                      // errorText={errorText}
                      // type={type}
                      // maxLength={maxLength}
                      // minLength={minLength}
                      // disabled={disabled}
                  />
              </td>
        </tr>
    )
}

export default TableInput