import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { useHistory, useParams } from 'react-router'
import MyTable from '../../../components/UI/MyTable/MyTable'
import MyButton from '../../../components/UI/MyButton/MyButton'
import Spinner from '../../../components/Spinner/Spinner'

import styles from './AcquirersDetails.module.scss'
import { deleteAcquirersThunk, getAcquirerDetailsThunk, setAcquirerDetails } from '../../../redux/actions/acquirerActions'
import DetailsTableRow from '../../../components/UI/MyTable/DetailsTableRow/DetailsTableRow'
import { Breadcrumb, BreadcrumbItem } from 'reactstrap'
import { NavLink } from 'react-router-dom'
import FlexBlock from '../../../components/Blocks/FlexBlock/FlexBlock'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'
import { useAlert } from '../../../hooks/useAlert'

const AcquirersDetails = ({
  className, details, getAcquirerDetailsThunk, setAcquirerDetails, deleteAcquirersThunk,
   ...props}) => {

  const {id} = useParams()
  const history = useHistory()

  const { successAlertVisible, setSuccessAlertVisible, successMessage,
    failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage, checkRes} = useAlert()


  const deleteAcquirer = () => {
    const confirmed = window.confirm("   ?");
    if (confirmed) {
        deleteAcquirersThunk([id])
        .then((res) => {
          checkRes()(res)
          return res
        })
        .then(res => {
            if (res.ok) {
                setTimeout(() => {
                  history.goBack()
                }, 1000)
            }
        })
        .then(() => console.log('deleteAcquirersThunk')
        )
        .catch((err) => console.log(err))
        
    }
  }

  useEffect(() => {
    getAcquirerDetailsThunk([id])
    return () => {
      setAcquirerDetails({
          uuid: '',
          active: true,
          alias: '',
          name: '',
        })
    }
    // eslint-disable-next-line 
  }, [])

    const cls = [
        styles.AcquirersDetails,
        className
    ]
    if (!details) {
      return <h1>  </h1>
  }

  if (!details.uuid) {
      return <Spinner/>
  }

  return (
    <div className={cls.join(' ')}>
        <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/acquirers' exact> </BreadcrumbItem>
          <BreadcrumbItem active tag="span">  {details.name}</BreadcrumbItem>
        </Breadcrumb>

        <center><h1>   </h1></center>
        <MyTable>
            <DetailsTableRow
                label='uuid'
                data={details.uuid}
            />
            <DetailsTableRow
                label=''
                data={details.name}
            />
            <DetailsTableRow
                label=''
                data={details.active? '' : ''}
            />
             <DetailsTableRow
                label='alias'
                data={details.alias}
            />

        </MyTable>

        <FlexBlock>
            <MyButton
                onClick={() => history.push(`/acquirers/update/${id}`)}
                buttonText=''
            />

            <MyButton
                color={'danger'}
                onClick={deleteAcquirer}
                buttonText=''
            />
        </FlexBlock>
        
        <SuccessAlert
                        visible={successAlertVisible}
                        setVisible={setSuccessAlertVisible}
                        message={successMessage}
                    />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />








    </div>
)
}

const mapStateToProps = (state) => {
return {
  details: state.acquirers.details,
}
}



export default connect(mapStateToProps, { 
  getAcquirerDetailsThunk, setAcquirerDetails, deleteAcquirersThunk
})(AcquirersDetails)