import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { Breadcrumb, BreadcrumbItem, Button, Form, FormGroup, Input, Label } from 'reactstrap'
import { getAccountDetailsThunk } from '../../../redux/actions/accountActions'

import styles from './AccountCreation.module.scss'

const AccountCreation = (props) => {

  const [controls, setControls] = useState({
    email: '',
    password: '',
    login: ''
  })
  const [touched, setTouched] = useState({
    email: false,
    password: false,
    login: false
  })

  const onControlChange = (e) => {
    const control = e.target
    setControls((controls) => {
      return {
        ...controls,
        [control.name]: control.value
      }
    })
    setTouched((touched) => {
      return {
        ...touched,
        [control.name]: true
      }
    })
  }

  const {email, password, login} = controls

    return (
        <div className={styles.AccountCreation}>
        <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/accounts' exact></BreadcrumbItem>
          <BreadcrumbItem active tag="span"> </BreadcrumbItem>
        </Breadcrumb>
            <h1> </h1>
            <Form>
              <FormGroup>
                <Label for="login"></Label>
                  <Input
                    type="text"
                    name="login"
                    id="login"
                    placeholder=""
                    value={login}
                    onChange={onControlChange}
                    required
                  />
              </FormGroup>
                  
              <FormGroup>
                <Label for="email">Email</Label>
                <Input
                  //  invalid={touched.email && !email}
                  type="email"
                  name="email"
                  id="email"
                  placeholder="email"
                  value={email}
                  onChange={onControlChange}
                />
              </FormGroup>
              <FormGroup>
                <Label for="password"></Label>
                <Input
                // invalid={touched.password && !password}
                  type="text"
                  name="password"
                  id="password"
                  placeholder=""
                  value={password}
                  onChange={onControlChange}
                />
              </FormGroup>
              <Button 
              disabled={!(email && login && password)}
              color='success'
              //  onClick={() => logInThunk(controls)}
              ></Button>
            </Form>
        </div>
    )
}

export default AccountCreation