import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { useHistory, useParams } from 'react-router'
import Spinner from '../../../components/Spinner/Spinner'
import TableInput from '../../../components/UI/MyTable/TableInput/TableInput'
import { useFormAndValidation } from '../../../hooks/useFormValiation'
import MyTableForm from '../../../components/UI/MyTable/MyTableForm/MyTableForm'

import styles from './ShopUpdate.module.scss'
import { getShopDetailsThunk, setShopDetails, updateShopThunk } from '../../../redux/actions/shopActions'
import { Breadcrumb, BreadcrumbItem } from 'reactstrap'
import { NavLink } from 'react-router-dom'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'
import { useAlert } from '../../../hooks/useAlert'

const ShopUpdate = ({
  className,  details,  getShopDetailsThunk, setShopDetails, 
  updateShopThunk, 
  ...props}) => {

    const { successAlertVisible, setSuccessAlertVisible, successMessage,
      failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage, checkRes} = useAlert()
  

  const {values, handleChange, errors, isValid, setValues, setIsValid } = useFormAndValidation()

  const {id} = useParams()
  const history = useHistory()

  useEffect(() => {
    getShopDetailsThunk([id])
    .then(checkRes(false))
    return () => {
      setShopDetails({
          uuid: '',
          partner_uuid: '',
          name: '',
        })
    }
    // eslint-disable-next-line 
  }, [])

  useEffect(() => {
    setIsValid(false)
    // eslint-disable-next-line
}, [])

useEffect(() => {
  setValues(details)
  // eslint-disable-next-line
}, [details])

const onSubmit = (evt) => {
  evt.preventDefault()
  updateShopThunk({uuid: id, ...values})
  .then((res) => {
    checkRes()(res)
    return res
  })
  .then(res => {
      if (res.ok) {
          setTimeout(() => {
            history.goBack()
          }, 1000)
      }
  })
  .catch((err) => console.log(err))
  console.log(values);
}

    const cls = [
        styles.ShopUpdate,
        className
    ]

    if (!details) {
      return <h1>  </h1>
  }

  if (!details.uuid) {
      return <Spinner/>
  }

    return (
        <div className={cls.join(' ')}>
         <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/partners' exact> </BreadcrumbItem>
          <BreadcrumbItem tag={NavLink} to={`/shops/${id}`} exact> </BreadcrumbItem>
          <BreadcrumbItem active tag="span">  &nbsp; <strong>{details.name}</strong></BreadcrumbItem>
        </Breadcrumb>
            <h1>    </h1>
            <MyTableForm
              onSubmit={onSubmit}
              isValid={isValid}
            >
              <TableInput
                    name='name'
                    label=''
                    required={true}
                    onChange={handleChange}
                    value={values.name}
                    errorText={errors.name}
                    minLength={1}
                    maxLength={199}
                />

            </MyTableForm>

                <SuccessAlert
                        visible={successAlertVisible}
                        setVisible={setSuccessAlertVisible}
                        message={successMessage}
                    />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />
        </div>
    )
}

const mapStateToProps = (state) => {
  return {
    details: state.shops.details,
  }
  }
  


  export default connect(mapStateToProps, {
    getShopDetailsThunk, setShopDetails, updateShopThunk
  })(ShopUpdate)