import React, { useEffect } from 'react'
import { Breadcrumb, BreadcrumbItem, Button } from 'reactstrap'
import { NavLink } from 'react-router-dom'
import styles from './PartnerAddition.module.scss'
import { useFormAndValidation } from '../../../hooks/useFormValiation'
import MyTable from '../../../components/UI/MyTable/MyTable'
import TableInput from '../../../components/UI/MyTable/TableInput/TableInput'
import { connect } from 'react-redux'
import { createPartnerThunk } from '../../../redux/actions/partnerActions'
import { useAlert } from '../../../hooks/useAlert'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'


const PartnerAddition = ({className, createPartnerThunk, ...props}) => {

    const {values, handleChange, errors, isValid, resetForm, setIsValid } = useFormAndValidation()
    const { successAlertVisible, setSuccessAlertVisible, successMessage, setSuccessMessage,
        failureAlertVisible, setFailureAlertVisible, failureMessage, setFailureMessage,
        checkRes} = useAlert()
   
        const cls = [
        styles.PartnerAddition,
        className
    ]

    useEffect(() => {
        setIsValid(false)
        // eslint-disable-next-line
    }, [])

    const onSubmit = (evt) => {
        evt.preventDefault()
        console.log(values);
        createPartnerThunk(values)
        .then((res) => {
            checkRes()(res)
            if (res.ok) resetForm()
        })

    }

    return (
        <div className={cls.join(' ')}>
          <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/partners' exact></BreadcrumbItem>
          <BreadcrumbItem active tag="span">  </BreadcrumbItem>
        </Breadcrumb>
            <h1> </h1>
            <form onSubmit={onSubmit} >
                <MyTable>
                    <TableInput
                         name='inn'
                        label=''
                        required={true}
                        onChange={handleChange}
                        value={values.inn}
                        errorText={errors.inn}
                        minLength={10}
                        maxLength={12}
                    />
                    <TableInput
                         name='name'
                         label=''
                        required={true}
                        onChange={handleChange}
                        value={values.name}
                        errorText={errors.name}
                        minLength={1}
                        maxLength={199}
                    />
                    {/* <TableInput
                         name='uuid'
                        label='uuid'
                        onChange={handleChange}
                        value={values.uuid}
                    /> */}
                    
                </MyTable>
                <Button 
                    color={isValid? "primary": 'secondary'}
                    disabled={!isValid}
                >
                    
                </Button>
            </form>


            <SuccessAlert
                visible={successAlertVisible}
                setVisible={setSuccessAlertVisible}
                message={successMessage}
            />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />
        </div>
    )
}

// const mapStateToProps = (state) => {
//     return {

//     }
// }



export default connect(null, { createPartnerThunk })(PartnerAddition)