import React from 'react'
import { useEffect } from 'react'
import { connect } from 'react-redux'
import { Input, InputGroup, InputGroupText, Table } from 'reactstrap'
import MyTable from '../../components/UI/MyTable/MyTable'
import MyInput from '../../components/UI/MyInput/MyInput'
import { getAccountsThunk } from '../../redux/actions/accountActions'

import styles from './Accounts.module.scss'

const Accounts = ({accounts, getAccountsThunk, history}) => {

    useEffect(() => {
        getAccountsThunk()
        // eslint-disable-next-line
    }, []) 

    console.log(accounts);


    return (
        <div className={styles.Accounts}>
            <center><h1> </h1></center>
            <MyTable>
                <tr>
                    <th>
                        
                    </th>
                    <td>
                        <MyInput/>
                    </td>
                </tr>
                <tr>
                    <th>
                        e-mail
                    </th>
                    <td>
                        <MyInput/>
                    </td>
                </tr>
                <tr>
                    <th>
                        
                    </th>
                    <td>
                        <MyInput/>
                    </td>
                </tr>
            </MyTable>

            <Table hover>
                <thead>
                    <tr>
                        <th>id</th>
                        <th></th>
                        <th>Email</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        accounts.map((account) => {
                            return (
                                <tr 
                                    key={account.id}
                                    className={styles.tr} 
                                    onClick={() => history.push(`/accounts/${account.id}`)}
                                >
                                    <th scope="row">{account.id}</th>
                                    <td>{account.login}</td>
                                    <td>{account.email}</td>
                                    <td>{account.role_names.join(', ')}</td>
                                </tr>
                            )
                        })
                    }
                    
                    
                </tbody>
            </Table>


        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        accounts: state.accounts.accounts
    }
  }

export default connect(mapStateToProps, {getAccountsThunk})(Accounts)